import React, { useState } from "react";
import ReactDOM from "react-dom";
import "./index.css";

export default function App() {
	let [data, setData] = useState({
		word: "",
		json: []
	});

	const getData = e => {
		e.preventDefault();
		clearForm();
		fetch(`https://ru.wikipedia.org/api/rest_v1/page/summary/${data.word}`)
			.then(response => response.json())
			.then(json => {
				let o = { ...data, json };
				setData(o);
			})
			.catch(e => console.error(e));
	};

	const updateWord = e_word => {
		clearForm();
		let o = { ...data, word: e_word };
		setData(o);
	};

	const clearForm = (e) => {
		let o = { ...data, json: [] };
		setData(o);
	}

	return (
		<div className="main">
			<h1>Wiki Parser</h1>
			<h2>Поиск информации в Википедии</h2>
			<div className="search">
				<form onSubmit={e => getData(e)}>
					<input
						style={{ padding: "5px", width: "215px" }}
						type="search"
						placeholder="Введите слово"
						onKeyUp={e => updateWord(e.target.value)}
						onChange={e => clearForm(e.target.value)}
					/>
				</form>
			</div>
			<div className="result">
				{data.json.title === "Not found." && (
					<p>Ничего не найдено</p>
				)}
				{data.json.originalimage && (
					<img src={data.json.originalimage.source} alt={data.json.displaytitle} />
				)}
				{data.json.extract && (
					<p>{data.json.extract}</p>
				)}
				{data.json.content_urls && (
					<p><a href={data.json.content_urls.desktop.page}>подробнее</a></p>
				)}
			</div>
		</div>
	);
}

ReactDOM.render(<App />, document.getElementById("root"));
